.reusableModal {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.4);

  &__content {
    padding: 20px;
    border-radius: 12px;
    background-color: #fff;
  }
}
